* {
  cursor: none;
}

body {
  overflow: hidden;
}

.WIP {
  min-height: 100px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.container {
  background-color: black;
  min-height: 100vh;
}

.activity-suggestion {
  color: white;
  display: flex;
  flex-direction: column;
  margin: 80px 24px 0px;
  text-align: center;
}

.cursor {
  position: absolute;
  top: 0;
  background: #6a1dc2;
  opacity: 88%;
  border-radius: 100%;
}
